import React from 'react';
import HeaderNavBar from '../HeaderNavBar'

const PADDING_SIZE = 50;

export default function ContainerT({ children, style, ...rest }) {
  return (
    <div
      style={{
        ...style,
        height: `${parseFloat(style.height) + PADDING_SIZE * 2}px`
      }}
      {...rest}>
      <HeaderNavBar />
      {children}
    </div>
  )
}