import React, { useState } from 'react';
import { connect } from 'react-redux';
import HeaderNavBarItem from '../HeaderNavBarItem';
import './HeaderNavBar.scss'
import * as actions from '../../actions'

const HeaderNavBar = ({ titles, delRow }) => {
  const [valueV, setValueV] = useState(null);
  return (
    <div className="element-navbar">
      {titles.map((item, index) => {
        return <HeaderNavBarItem key={item} valueV={valueV} setValueV={setValueV} id={index} title={item} />
      })}
      <div className="element-navbar__item ">
        <button type="button" onClick={delRow} className="element-navbar__sort-btn">Delete rows</button>
      </div>
    </div>
  )
}
const mapStateToProps = (state) => {
  return {
    value: state.data,
    titles: state.arrOfTitles
  }
}

export default connect(mapStateToProps, actions)(HeaderNavBar);