import React from 'react';
import { connect } from 'react-redux';
import './HeaderNavBarItem.scss'
import * as actions from '../../actions'

const HeaderNavBarItem = ({ title, valueV, setValueV, id, delCol, sortUp, filterValue, sortDown }) => {

  return (
    <div className="element-navbar__item ">
      <span>{title}</span>
      <button
        type="button"
        onClick={() => {
          setValueV(id + 10);
          sortUp(id)
          console.log(valueV);
        }}
        className={valueV === id + 10 ? "element-navbar__sort-btn-secondary" : "element-navbar__sort-btn"}>&#x2191;</button>
      <button
        type="button"
        onClick={() => {
          setValueV(id);
          sortDown(id)
          console.log(valueV);
        }}
        className={valueV === id ? "element-navbar__sort-btn-secondary" : "element-navbar__sort-btn"}>&#x2193;</button>
      <button
        type="button"
        onClick={() => {
          delCol(id)
        }}
        className="element-navbar__sort-btn">Del Col</button>
      <button
        type="button"
        onClick={() => filterValue(id)}
        className="element-navbar__search-button" />
    </div>
  )
}
const mapStateToProps = (state) => {
  return {
    value: state.data
  }
}
export default connect(mapStateToProps, actions)(HeaderNavBarItem);